<template>
    <div class="login">

        <!-- Modal -->
        <div class="modal fade" id="login" tabindex="-1" aria-labelledby="login" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content p-4">


                    <ul class="nav nav-fill nav-pills mb-3" id="pills-tab" role="tablist">
                        <li class="nav-item">
                            <a class="nav-link active py-2" id="pills-home-tab" data-toggle="pill" href="#pills-login"
                                role="tab" aria-controls="pills-login" aria-selected="true">Login</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link py-2" id="pills-register-tab" data-toggle="pill" href="#pills-register"
                                role="tab" aria-controls="pills-register" aria-selected="false">Signup</a>
                        </li>
                    </ul>

                    <div class="tab-content" id="pills-tabContent">

                        <!-- Login Modal -->
                        <div class="tab-pane fade show active" id="pills-login" role="tabpanel"
                            aria-labelledby="pills-login-tab">

                            <h5 class="text-center">Login Please</h5>
                            <div class="form-group">
                                <label for="exampleInputEmail1">Email address</label>
                                <input type="email" v-model="email" class="form-control" id="exampleInputEmail1"
                                    aria-describedby="emailHelp" placeholder="Enter email">
                                <small class="form-text text-muted">We'll never share your email with anyone
                                    else.</small>
                            </div>
                            <div class="form-group">
                                <label for="exampleInputPassword1">Password</label>
                                <input type="password" @keyup.enter="login" v-model="password" class="form-control"
                                    id="exampleInputPassword1" placeholder="Password">
                            </div>

                            <div class="form-group">
                                <button class="btn btn-primary px-5" @click="login">Login</button>
                            </div>

                        </div>


                        <!-- SignUp Modal -->
                        <div class="tab-pane fade" id="pills-register" role="tabpanel"
                            aria-labelledby="pills-register-tab">

                            <h5 class="text-center">Create New Account</h5>

                            <div class="form-group">
                                <label for="name">Your name</label>
                                <input type="text" v-model="name" class="form-control" id="name"
                                    placeholder="Your nice name">
                            </div>

                            <div class="form-group">
                                <label for="email">Email address</label>
                                <input type="email" v-model="email" class="form-control" id="email"
                                    aria-describedby="emailHelp" placeholder="Enter email">
                            </div>
                            <div class="form-group">
                                <label for="password">Password</label>
                                <input type="password" @keyup.enter="register" v-model="password" class="form-control" id="password"
                                    placeholder="Password">
                            </div>

                            <div class="form-group">
                                <button class="btn btn-primary px-5" @click="register">Signup</button>
                            </div>

                        </div>

                    </div>

                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import { fb, db } from '../firebase'

    export default {
        name: 'Login',
        data() {
            return {
                name: null,
                email: null,
                password: null
            }
        },
        methods: {

            login() {
                fb.auth().signInWithEmailAndPassword(this.email, this.password)
                    .then((user) => {
                        $("#login").modal('hide')
                        this.$router.replace('admin')
                        Toast.fire({
                            icon: 'success',
                            title: 'Logged In Successfully!'
                        })
                    })
                    .catch((error) => {
                        var errorCode = error.code;
                        var errorMessage = error.message;
                        if (errorCode === 'auth/wrong-password') {
                            // alert('Wrong password.');
                            Toast.fire({
                                icon: 'warning',
                                title: 'Enter valid Username and Password!'
                            })
                        } else {
                            alert(errorMessage);
                        }
                        console.log(error);
                    });
            },

            register() {
                fb.auth().createUserWithEmailAndPassword(this.email, this.password)
                    .then((user) => {
                        $("#login").modal('hide')
                        // console.log(user.user.uid);

                        db.collection("profiles").doc(user.user.uid).set({
                            name: this.name
                        })
                        .then(function() {
                            console.log("Document successfully written!");
                        })
                        .catch(function(error) {
                            console.error("Error writing document: ", error);
                        });

                        this.$router.replace('admin')
                        Toast.fire({
                            icon: 'success',
                            title: 'Signed In Successfully'
                        })
                    })
                    .catch((error) => {
                        // Handle Errors here.
                        var errorCode = error.code;
                        var errorMessage = error.message;
                        if (errorCode == 'auth/weak-password') {
                            // alert('The password is too weak.');
                            Toast.fire({
                                icon: 'warning',
                                title: 'The Password is too weak!'
                            })
                        } else {
                            alert(errorMessage);
                        }
                        console.log(error);
                    });
            }

        }
    }
</script>

